@import url(https://fonts.googleapis.com/css2?family=Yomogi&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Yomogi', cursive;
  box-sizing: border-box;
  margin: 0;
  padding: 0;

}

.page-title{
  font-family: 'Abril Fatface', cursive;
  font-size: 3.5rem;
  font-weight: bold;
}

a:hover {
  font-weight: bold;
}

body{
  background-color: #191919;
}
.navbar {
    height: 80px;
    display: flex;
    position: fixed;
    justify-content: start;
    align-items: center; 
    z-index: 9;
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    color: #121212;
    /* background-color: none; */

}

.nav-menu {
    background-color: #121212;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 999;

} 

.nav-menu.active{
    left: 0;
    transition: 350ms;
}  

.nav-text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px; 
} 

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
} 

.nav-text a:hover{
    background-color: #539db6;
} 

.nav-menu-item{
    width: 100%;
}

.nav-menu-toggle{
    background-color: #121212;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span{
    margin-left: 16px;
}


.header {
  display: inline-block;
  width: 50%;
  top: 0;
}
.headerImage{
  /* width: 100%; */
  height: 100vh;
  right: 0;
}
.header-nav{
  float: right;
  top: 0;
}


.headerText{
  color: #cbcbcb;
  font-family: 'Abril Fatface', cursive;
  font-size: 3.5rem;
  font-weight: 900;
  text-transform: uppercase;
  margin-right: 10px;
  margin-top: 3px;
  margin-bottom: 5px;
  float: right;
}
.linkText{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 3.5rem;
  font-weight: 400;
  margin: 10px;
  float: right;

  
  
}
.link{
    color: #666;
    text-decoration: none;
}
.link:hover{
  color:rgb(83, 157, 182);
}

.about-page{
    padding: 100px 50px;
    height: 100vh;
    background-color: #bfbfbf;
    align-items: center;
    z-index:-999;
}

.about-content{
    display: inline-block;
    width: 50%;
}

.bg-box{
    width: 32%;
    height: 450px;
    background-color: #191919;
    margin: 135px 60px 0 65px;

    border: none;
    top: 0;
    position: absolute;
    z-index: 1;
}

.about-img{
    width: 70%;
    display: flex;
    position: relative;
    align-items: center;
    margin: auto;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
    z-index: 2;
}

.about-header-title{
    width: 70%;
    display: start;
    
    align-items: left;
    margin: 0;
}
.about-text{
    width: 70%;
    align-items: left;
    margin: 0;
    
}
.project-page{
    padding: 50px;
    align-items: center;
    float: right;
    height: 100vh;
    width: 100%;
    background-color: #bfbfbf;
}
.project-header-title{
    margin-left: 5%;
}
.project-content{
    
    margin: auto;
    
}
.project-img{
    display: inline-block;
    width: 29%;
    margin: 20px;
}
.skill-page{
    padding: 100px;
    background-color: #bfbfbf;
}
.skill-sub-title{
    text-transform: uppercase;
}
.skill, .tool{
    display: inline-block;
    width: 150px;
    height: 175px;
    padding: 20px;
    margin: 15px;
    background-color: #fff;

    font-size: 18px;
    font-weight: bold;
    text-align: center;
}
.skill-img, .tool-img{
    width: 90%;
}

.contact-page{
    background-color: #191919;
    height: 100vh;
}

.contact-content{display: inline-block;}
.content-left{padding-right: 50px;}
.content-right{padding: 100px 0 0 100px}

/* ---------------Left side ----------------*/
.contact-header-title{
    color: #f2f2f2;
    margin-left: 100px;
    
}

.interested-list{
    color: #cbcbcb;
    font-size: 2em;
    margin-left: 100px;
    list-style-type: none;
    
}
.download-button{
    background-color: #f2f2f2;
    padding: 5px 20px;
    border: none;
    border-radius: 5px;
    margin-left: 30%;
    margin-top: 50px;

}
.download-button:hover{
    background-color: #539db6;
    transition: 100ms;
}
.download{
    width: 100%;
    height: 100%;
    font-size: 2em;
    font-weight: bold;
    text-decoration: none;
    color: black;
}
.download:hover{
    color: #fff;
    transition: 100ms;
}


/* ---------------Right side ----------------*/
.stamp{
    width: 100%;
    right: 0;
    align-items: flex-end;
    margin-left: 20%;
    padding: 0;
}

.name{
    color: #f2f2f2;
    font-size: 3em;
    font-weight: bold;
    margin: 10px 50px 5px 0;
}

.contact-info{
    color: #ababab;
    font-size: 1.5em;
   
}

.contact-link{
    color: #f2f2f2;
   

}

.vertical-line{
    border-left: 6px solid #539db6;
    height: 75%;
    margin-top: 5%;
    position: absolute;
    
}

.contact-link:hover {
    text-decoration: none;
    color: #539db6;
}
