.navbar {
    height: 80px;
    display: flex;
    position: fixed;
    justify-content: start;
    align-items: center; 
    z-index: 9;
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    color: #121212;
    /* background-color: none; */

}

.nav-menu {
    background-color: #121212;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 999;

} 

.nav-menu.active{
    left: 0;
    transition: 350ms;
}  

.nav-text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px; 
} 

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
} 

.nav-text a:hover{
    background-color: #539db6;
} 

.nav-menu-item{
    width: 100%;
}

.nav-menu-toggle{
    background-color: #121212;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span{
    margin-left: 16px;
}
